nav {
    height: 50px;
    background-color: #271923;

    ul {
        li {
        
        color: $color-bg;
        display: inline-block;
        font-family: $font-title;
        font-weight: 300;
        font-size: 1.6rem;
        padding: 0 20px;
        line-height: 50px;

        a {
            text-decoration: none;
            color: $color-bg;
            &:hover, &:focus, &:active {
                text-decoration: none;
                color: lighten($color-primary, 10%);
            }
        }

        &:first-of-type {
            font-weight: 900;
            padding-left: 0;
        }

        &:last-of-type {
            float: right;
            font-size: 1.4rem;
            padding-right: 0;
        }
    }   
    }
}